<template>
  <div id="thisIsInformationDetail-xiaohai" v-loading="loading">
    <div class="title">{{this.list[0].title}}</div>
    <div class="comeFrom">
      <div class="come">来源：</div>
      <div class="from">{{this.list[0].source}}</div>
    </div>
    <!-- <div>
      <img class="detailImg" :src="this.list[0].imageURL">
    </div> -->
    <div
      id="content"
      class="contentDetail"
      v-html="this.list[0].content"
      ref="p"
    ></div>
    <div class="publish">发表评论</div>
    <textarea class="critical" ref="submitContent" placeholder="请输入您的评论" @blur="getSubmitContent"></textarea>
    <button class="submit" @click="submitReview()">提交</button>
    <div
      ref="towlistHandler"
      class="towList"
      @click="go"
      v-if="this.list.length ===2"
      :class="{'show':this.isshow == true}"
    >
      <div class="tow">下一篇</div>
      <div class="twoTitle">{{this.list[1].title}}</div>
      <div class="towContent" v-html="this.list[1].summary">{{this.list[1].summary}}</div>
      <div class="towTime">{{this.list[1].updateTime}}</div>
    </div>
    <div class="allMessage" :class="{'show':this.count == 0}">
      <h3>评论列表</h3>
      <div class="message" v-for=" item in this.message " :key="item.id">
        <div>{{item.user_id}}:</div>
        <div class="userContent">{{item.content}}</div>
        <div class="createTime">{{item.create_time}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      list: [
        {
          content: "",
          id: "",
          imageURL: "",
          source: "",
          summary: "",
          title: "",
          updateTime: ""
        },
        {
          content: "",
          id: "",
          imageURL: "",
          source: "",
          summary: "",
          title: "",
          updateTime: ""
        }
      ],
      submitContent: "",
      isshow: false,
      message: [],
      count: 0
    };
  },
  created() {
    this.getData();
    this.getMessage();
  },
  mounted() {
    this.monitor();
  },
  deactivated() {
    this.$destroy();
  },
  methods: {
    //点击进入页面详情查找
    getData() {
      this.loading = true;
      this.$axios
        .get("/article/details", {
          params: {
            id: this.$route.params.id
          }
        })
        .then(res => {
          this.list = res.data;
          console.log(this.list);
          this.loading = false;
        });
    },
    //提交评论执行请求方法
    submitReview() {
      //获取sess中个人资料
      let userString = sessionStorage.getItem("user");
      //判断是否为登录状态
      if (userString == null) {
        this.tipsOn();
      } else {
        this.user_id = JSON.parse(userString).phone;
        //在登陆状态下提交评论时判断内容是否为空
        if (this.submitContent == "") {
          this.isNull();
        } else {
          //在登录状态下提交评论不为空时允许提交到后台服务器进行审核
          this.$axios
            .post("/leaveMessage/addLeaveMessage/", {
              content: this.submitContent,
              user_id: this.user_id,
              recipient_id: this.$route.params.id
            })
            .then(res => {
              //判断提交是否成功
              if (res.data.msg === "留言出错") {
                this.defeated();
              } else {
                //提交成功后的提示窗口
                this.success();
                //清空评论输入框
                this.$refs.submitContent.value = "";
              }
            });
        }
      }
    },
    //第二篇详情跳转
    go() {
      this.$router.push("/news/informationDetail/" + this.list[1].id);
      this.getData();
      this.getMessage();
    },
    //提交评论失败后提示框显示方法
    defeated() {
      this.$alert("提交失败", "评论提交", {
        dangerouslyUseHTMLString: true
      });
    },
    //提交评论成功后提示框显示方法
    success() {
      this.$alert("提交成功等待审核", "评论提交", {
        dangerouslyUseHTMLString: true
      });
    },
    //请登陆后在评论
    tipsOn() {
      this.$alert("请登录您的账号再提交", "温馨提示", {
        dangerouslyUseHTMLString: true
      });
    },
    //提交评论为空
    isNull() {
      this.$alert("请填写内容后提交", "温馨提示", {
        dangerouslyUseHTMLString: true
      });
    },
    //获取评论框文本的方法
    getSubmitContent() {
      this.submitContent = this.$refs.submitContent.value;
      console.log(this.submitContent);
    },
    //监听滑动条
    monitor() {
      window.addEventListener("scroll", () => {
        //获取目前滚动的长度是多少
        let rollingLength = document.documentElement.scrollTop;
        //获取整个滚动条长度
        let totalLength = document.documentElement.scrollHeight;
        //到底部还有多长
        let toBottom = totalLength - rollingLength;
        if (toBottom < 900) {
          this.isshow = true;
        } else {
          this.isshow = false;
        }
      });
    },
    //获取评论列表
    getMessage() {
      this.$axios
        .get("/leaveMessage/queryLeaveMessageList", {
          params: {
            page: 1,
            limit: 15,
            recipient_id: this.$route.params.id
          }
        })
        .then(res => {
          console.log(res.data);
          this.message = res.data.data;
          this.count = res.data.count;
        });
    }
  }
};
</script>

<style lang="scss">
#thisIsInformationDetail-xiaohai {
  width: 64%;
  padding-left: 1%;
  .title {
    margin-top: 80px;
    font-size: 30px;
    margin-top: 20px;
    width: 100%;
    font-weight: 500;
    line-height: 45px;
    color: #262626;
  }
  .comeFrom {
    margin-top: 30px;
    height: 30px;
    font-size: 14px;
    line-height: 30px;
    .come {
      float: left;
      height: 100%;
      line-height: 100%;
      padding-top: 0.2%;
    }
    .from {
      float: left;
      background-color: #4285f4;
      border-radius: 4px;
      padding: 0 22px 0 22px;
      height: 20px;
      line-height: 20px;
      color: #eee;
    }
  }
  .detailImg {
    position: relative;
    top: 0;
    height: 350px;
    width: 100%;
  }
  .content {
    font-size: 16px;
    line-height: 30px;
    width: 100%;
    margin-left: 2.9%;
    padding-top: 20px;
    color: #000000;
    padding: 0;
    margin: 0;
  }
  .contentDetail {
    font-size: 18px;
    line-height: 30px;
    width: 100%;
    padding-top: 20px;

    color: #000000;
    padding: 0;
    margin: 0;
    padding-top: 20px;
  }
  .publish {
    font-size: 18px;
    margin-top: 30px;
  }
  .critical {
    outline: none;
    height: 150px;
    width: 98%;
    border: 1px solid #7f7f7f;
    padding: 7px;
    margin-top: 20px;
    resize: none;
    font-size: 16px;
    color: #000000;
  }
  .submit {
    height: 50px;
    width: 19.5%;
    border-radius: 30px;
    outline: none;
    margin-top: 50px;
    margin-left: 80%;
    margin-bottom: 30px;
    background-color: #a30014;
    color: #eee;
    cursor: pointer;
  }
  .show {
    display: none;
  }
  .towList {
    background: #fff;
    width: 16%;
    border: 1px solid #e4e7ed;
    position: fixed;
    top: 100px;
    right: 8%;
    border-top: 4px solid #4285f4;
    padding: 15px;
    border-radius: 2px;
    cursor: pointer;
    .tow {
      font-size: 18px;
      font-weight: 600;
    }
    .contentTitle {
      font-size: 16px;
      margin-top: 10px;
      margin-left: 1.4%;
      margin-right: 1.4%;
      line-height: 16px;
      text-indent: 1.5em;
    }
    .twoTitle {
      color: #4285f4;
      font-size: 16px;
      padding-top: 16px;
      text-align: center;
    }
    .towContent {
      color: #5a5a5a;
      padding-top: 30px;
      font-size: 14px;
      line-height: 24px;
    }
    .towTime {
      font-size: 12px;
      color: #5a5a5a;
      text-align: right;
      margin-top: 20px;
    }
  }
  .allMessage {
    width: 100%;
    h3 {
      padding-bottom: 30px;
    }
    .message {
      width: 100%;
      padding: 15px 15px 15px 15px;
      border: 1px solid #7f7f7f;
      vertical-align: top;
      .userContent {
        display: block;
        font-size: 14px;
        line-height: 20px;
        padding-top: 8px;
        text-indent: 2em;
      }
      .createTime {
        text-align: right;
        padding-top: 15px;
        font-size: 12px;
        padding-right: 10px;
      }
    }
  }
}
</style>
