<template>
  <div class="mineadress" v-loading="loading">
    <div class="title">
      <h1>我的川料</h1>
    </div>
    <div class="myAddress" v-if="list.length == 0">您暂时没有添加收货地址，请点击下方按钮添加收货地址！</div>
    <div class="myAddress" v-for="item in list" :key="item.id">
      <div class="myAddressTitle">{{item.name}}</div>
      <div class="myAddressName">收货人：{{item.name}}</div>
      <div class="myAddressHome">收货地址：{{item.address}}</div>
      <div class="myAddressPhone">手机：{{item.rec_phone}}</div>
      <div class="delete" @click="deleteAddress(item.id)">删除</div>
      <div class="edit" @click="information(item.id)">编辑</div>
      <div class="edit" v-if="item.status == 0" @click="setDefault(item.id)">设为默认地址</div>
      <div class="edit" v-if="item.status == 1">默认地址</div>
    </div>
    <div class="add_box" @click="clickAdd">添加新地址</div>
    <div class="jumpbox" v-show="display">
      <div class="con_left">
        <input type="text" placeholder="收件人：">
        <br>
        <input type="text" placeholder="详细地址：">
      </div>
      <div class="con_right">
        <input type="text" placeholder="选择省/市/区">
        <br>
        <VDistpicker class="selec"/>
        <input type="text" placeholder="电话：">
      </div>
      <div class="btn" @click="jumpback">保存并使用</div>
    </div>
    <el-dialog title="修改收货地址" :visible.sync="dialogFormVisible">
      <div class="editAddessInput">收货人：</div>
      <el-input v-model="editAddress.name" placeholder="请输入修改的收货人"></el-input>
      <div class="editAddessInput">收货人地址：</div>
      <el-input v-model="editAddress.address" placeholder="请输入修改的收货人地址"></el-input>
      <div class="editAddessInput">收货人电话：</div>
      <el-input v-model="editAddress.rec_phone" placeholder="请输入修改的收货人电话"></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="ditAddress">确认修改</el-button>
      </div>
    </el-dialog>
    <el-dialog title="添加收货地址" :visible.sync="isdialogFormVisible">
      <div class="editAddessInput">收货人：</div>
      <el-input v-model="addAddress.name" placeholder="请输入添加的收货人"></el-input>
      <div class="editAddessInput">收货人地址：</div>
      <el-input v-model="addAddress.address" placeholder="请输入添加的收货人地址"></el-input>
      <div class="editAddessInput">收货人电话：</div>
      <el-input v-model="addAddress.phone" placeholder="请输入添加的收货人电话"></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isdialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="isaddAddress">确认添加</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import VDistpicker from "v-distpicker";
import { log } from "util";
export default {
  components: { VDistpicker },
  data() {
    return {
      loading: true,
      display: false,
      dialogTableVisible: false,
      dialogFormVisible: false,
      isdialogTableVisible: false,
      isdialogFormVisible: false,
      list: [
        {
          id: "",
          name: "",
          address: "",
          phone: ""
        }
      ],
      editAddress: {
        id: "",
        name: "",
        address: "",
        rec_phone: ""
      },
      addAddress: {
        name: "",
        address: "",
        phone: ""
      }
    };
  },
  created() {
    this.getAddress();
  },
  methods: {
    jump() {
      this.display = true;
    },
    jumpback() {
      this.display = false;
    },
    //进入页面获取该账号的收货地址
    getAddress() {
      this.loading = true;
      //获取sess中个人资料
      let userString = sessionStorage.getItem("user");
      //保存用户id到data中等待使用
      this.user_id = JSON.parse(userString).phone;
      this.$axios
        .get("/user/queryRecInfo", {
          params: {
            user_phone: this.user_id
          }
        })
        .then(res => {
          this.list = res.data.data;
          this.loading = false;
        });
    },
    //传入id后根据id后台删除该地址
    deleteAddress(id) {
      console.log(id);
      this.$axios
        .post("/user/removeRecInfo", {
          id: id
        })
        .then(res => {
          this.getAddress();
        });
    },
    //点击编辑编辑用户的收货地址
    information(id) {
      console.log(this);
      this.list.forEach(item => {
        if (item.id == id) {
          (this.editAddress.id = item.id),
            (this.editAddress.name = item.name),
            (this.editAddress.address = item.address),
            (this.editAddress.rec_phone = item.rec_phone);
        }
      });
      this.dialogFormVisible = true;
      console.log(id);
    },
    //弹出框提示
    open(title, message) {
      this.$alert(message, title, {
        confirmButtonText: "确定",
        callback: action => {}
      });
    },
    //点击新增收获地址按钮
    clickAdd() {
      console.log("in");
      this.isdialogFormVisible = true;
    },
    //提交修改接口调用
    ditAddress() {
      //获取sess中个人资料
      let userString = sessionStorage.getItem("user");
      //保存用户id到data中等待使用
      this.user_id = JSON.parse(userString).phone;
      if (
        this.editAddress.name == "" ||
        this.editAddress.rec_phone == "" ||
        this.editAddress.address == ""
      ) {
        this.open("修改收货地址", "请勿修改提交空白的收货信息");
      } else {
        this.$axios
          .post("/user/modifyRecInfo", {
            id: this.editAddress.id,
            name: this.editAddress.name,
            user_phone: this.user_id,
            rec_phone: this.editAddress.rec_phone,
            address: this.editAddress.address
          })
          .then(res => {
            this.getAddress();
            this.dialogFormVisible = false;
          });
      }
    },
    //添加收货地址
    isaddAddress() {
      //获取sess中个人资料
      let userString = sessionStorage.getItem("user");
      //保存用户id到data中等待使用
      this.user_id = JSON.parse(userString).phone;
      if (
        this.addAddress.name === "" ||
        this.addAddress.phone == "" ||
        this.addAddress.address === ""
      ) {
        this.open("添加收货地址", "请勿添加空白的收货信息");
      } else {
        this.$axios
          .post("/user/addRecInfo", {
            name: this.addAddress.name,
            user_phone: this.user_id,
            rec_phone: this.addAddress.phone,
            address: this.addAddress.address
          })
          .then(res => {
            this.getAddress();
            this.isdialogFormVisible = false;
            this.addAddress.name = "";
            this.addAddress.address = "";
            this.addAddress.phone = "";
          });
      }
    },
    //将收货地址设置为默认收货地址
    setDefault(id) {
      this.$axios
        .post("/user/modifyRecInfoDefaultStatus", {
          id: id
        })
        .then(res => {
          this.getAddress();
        });
    }
  }
};
</script>

