<template>
  <div class="news-wrap">
    <!-- 搜索内容 -->
    <div class="search-top">
      <el-input class="search" @change="getval" placeholder="请输入搜索内容" v-model="inputval" clearable></el-input>
      <el-button
        class="el-icon-caret-botton"
        type="danger"
        icon="el-icon-search"
        autofocus
        v-on:click="getval"
      >搜索</el-button>
    </div>
    <!-- 新闻内容 -->
    <div v-if="id==1">
      <NewsTemplate/>
    </div>
    <!-- 搜索新闻 -->
    <div class="news-box" v-loading="loading" v-if="id ==2">
      <div v-for="item in searchList" :key="item.id">
        <div @click="go(item.id)" class="new-content">
          <div class="new-img">
            <img :src="item.imageURL" alt="">
          </div>
          <div class="new-text">
            <p class="template-title">
              <a href="javascript:;">{{item.title}}</a>
            </p>
            <p class="template-content">
              <a href="javascript:;">{{item.summary}}</a>
            </p>
            <p class="template-bar">
              <span class="template-from">
                来自主题：
                <a href="javascript:;">{{item.source}}</a>
              </span>
              <span class="template-time">
                <i class="el-icon-time"></i>
                {{item.updateTime}}
              </span>
            </p>
          </div>
        </div>
      </div>
      <!-- <div
      class="pagination"
      @click="tablePagination()"
      :class="{'show': this.number === this.count}"
      >点击加载更多</div>-->
    </div>
    <!-- 没有搜索到内容 -->
    <div class="notFound" v-if="id==3">
      <div class="notFound-wrap">
        <img src="@/assets/img/notFound.png" class="sub">
        <div class="sorry">
          很抱歉，没有找到“
          <span>{{inputval}}</span>”相关结果
        </div>
        <div class="change">请修改或者尝试其他搜索词</div>
      </div>
    </div>
  </div>
</template>

<script>
import NewsTemplate from "@/components/NewsTemplate";
import { log } from "util";
export default {
  name: "News",
  data() {
    return {
      list: [],
      page: "1",
      limit: 3,
      length: 2,
      count: 1,
      number: 2,
      loading: true,
      id: 1,
      inputval: "",
      searchList: [],
      flag: true
    };
  },
  components: {
    NewsTemplate
  },
  created() {
    this.getPageData(this.page, this.limit);
    this.getData();
    if (this.$route.params.id) {
      this.id = this.$route.params.id;
      this.searchList = this.list;
    } else if (this.$route.query.id) {
      this.id = this.$route.query.id;
    }
  },
  methods: {
    go(id) {
      this.$router.push("/news/informationDetail/" + id);
    },
    tablePagination() {
      this.getPageData(this.page, this.limit);
    },
    handleCurrentChange(val) {
      this.page = val;
    },
    getPageData(page, limit) {
      this.loading = true;
      this.$axios
        .get("/article/list", {
          params: {
            page: page,
            limit: limit
          }
        })
        .then(res => {
          this.limit = this.limit + 3;
          this.list = res.data.data;
          this.number = res.data.data.length;
          this.loading = false;
        });
    },
    getData() {
      this.$axios.get("/article/list").then(res => {
        // console.log(res)
        this.length = this.length + res.data.data.length;
        this.count = res.data.count;
      });
    },
    //获取input值
    getval() {
      this.loading = true;
      this.id = 2;
      var val = this.inputval;
      this.getNewData(val);
    },
    //点击搜索后请请求数据
    getNewData(val) {
      var that = this;
      this.$axios
        .get("/article/list", {
          params: {
            title: val
          }
        })
        .then(res => {
          // console.log(res)
          if (res.data.data == "") {
            that.id = 3;
            this.loading = false;
          } else if (res.data.data !== "") {
            that.searchList = res.data.data;
            this.loading = false;
          }
          // console.log(that.inputval)
        });
    }
  },
  deactivated() {
    this.$destroy();
  }
};
</script>
<style lang="scss" >
.news-wrap {
  width: 100%;
}
.news-box {
  padding-top: 20px;
  margin-top: 57px;
  .new-title {
    color: #262626;
    font-size: 24px;
    line-height: 33px;
    padding-bottom: 14px;
    font-weight: 500;
    h3 {
      font-size: 24px;
    }
  }

  .new-content {
    margin-bottom: 30px;
    width: 97%;
    margin-left: 10px;
    cursor: pointer;
    display: flex;
    height: 160px;
    padding: 10px;
    box-shadow: 0 0 13px rgba(0, 0, 0, 0.1);
    transition: all 0.4s;
    &:hover {
      box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.5);
      transform: translate(0px, -3px);
    }

    .new-img {
      width: 216px;
      height: 100%;
      overflow: hidden;

      img {
        height: 100%;
        transition: all 1s;
        transform: scale(1);
      }
    }

    .new-text {
      height: 100%;
      margin-left: 20px;
      position: relative;
      width: calc(100% - 236px);

      .template-title {
        line-height: 24px;
        margin-top: 8px;
        margin-bottom: 10px;
        max-height: 48px;

        a {
          color: #262626;
          font-size: 18px;
          text-align: justify;
          transition: 0.5s;

          &:hover {
            color: #4285f4;
          }
        }
      }

      .template-content a {
        color: #787878;
        font-size: 13px;
        letter-spacing: 0;
        line-height: 20px;
        margin-top: 10px;
        max-height: 40px;

        &:hover {
          color: #797979;
        }
      }

      .template-bar {
        bottom: 2px;
        color: #a7a7a7;
        float: left;
        font-size: 12px;
        line-height: 18px;
        position: absolute;
        width: 100%;

        .template-from {
          padding-right: 6px;

          a {
            color: #007bff;
          }
        }

        .template-time {
          float: right;
          padding-left: 5px;
        }
      }
    }
  }
  .pagination {
    position: relative;
    top: 30px;
    left: 440px;
    height: 36px;
    width: 150px;
    border: 2px solid #007bff;
    line-height: 36px;
    text-align: center;
    color: #007bff;
    cursor: pointer;
  }
  .show {
    display: none;
  }
}
// 没有找到数据
.notFound {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-top: 90px;
  overflow: hidden;
  .notFound-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    .sorry {
      padding-top: 30px;
      padding-bottom: 30px;
      color: #3d464d;
      font-size: 18px;
      span {
        font-size: 16px;
        color: #ff4343;
      }
    }
    .change {
      font-size: 17px;
      color: #999;
    }
  }
}
// 搜索框
.search-top {
  width: 80%;
  height: 50px;
  // background: yellow;
  margin: 0px auto;
  display: flex;
  padding-top: 50px;
  .search {
    height: 49px;
    width: 80%;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: 1px solid #e4e7ed;
    border-right: none;
    input.el-input__inner {
      border-color: #fff !important;
      height: 49px;
    }
    input.el-input__inner:hover {
      border-color: #fff;
    }
  }
  .el-icon-caret-botton {
    border: none;
    height: 51px;
    width: 20%;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    background-color: #bf0000;
    outline: 0px;
  }
}
</style>
