<template>
  <div class="wrap">
    <div class="detail">
      <ul class="left-smallImg">
        <li v-for="item in detail.images" :key="item.id">
          <img :src="item" alt="商品示例" width="100%" height="100%">
        </li>
      </ul>
      <div class="zoom">
        <div class="smallImg">
          <img :src="detail.bigPicture" alt="商品展示">
          <span class="zoomspan"></span>
        </div>
        <div class="bigImg">
          <img :src="detail.bigPicture" alt="商品展示" class="pic">
        </div>
      </div>
    </div>
    <div class="middle">
      <div class="detail-decs" s>
        <h1>{{this.detail.formulaName}}</h1>
        <p class="text">
          说明：
          <span>{{this.detail.formulaDescription}}</span>
        </p>
        <p class="price">{{this.detail.formulaPrice}}元</p>
      </div>
      <el-steps :space="200" :active="active" finish-status="success">
        <el-step title="试经营"></el-step>
        <el-step title="确认购买" class="up"></el-step>
        <el-step title="日期预约" id="maodian"></el-step>
        <el-step title="料厂实操" class="up"></el-step>
        <el-step title="售后确认"></el-step>
        <el-step title="学习结束" class="up"></el-step>
      </el-steps>
      <el-button class="dialog-btn1" type="text" @click="dialogVisible1 = true"></el-button>
      <el-button class="dialog-btn2" type="text" @click="dialogVisible2 = true"></el-button>
      <el-button class="dialog-btn3" type="text" @click="appointmentTimeConfirm"></el-button>
      <el-button class="dialog-btn4" type="text" @click="dialogVisible4 = true"></el-button>
      <el-button class="dialog-btn5" type="text" @click="dialogVisible5 = true"></el-button>
      <el-button class="dialog-btn6" type="text" @click="dialogVisible6 = true"></el-button>
      <!-- 试经营 -->
      <el-dialog title="" :visible.sync="dialogVisible1" @click="dialogVisible1 = false">
        <span class="commodity-info">
          <div class="bigImg">
            <img :src="materialDetail.bigImage" alt="商品展示">
          </div>
          <div class="decs">
            <h1>{{materialDetail.tradeName}}</h1>
            <p class="text">规格：{{materialDetail.content}}</p>
            <p class="price">{{materialDetail.price}}元-{{materialDetail.price}}元</p>
            <p>
              <button @click="addToCart">加入购物车</button>
              <!-- <button class="green">一键购</button> -->
            </p>
            <!-- <img src="../assets/img/top-cart.png"> -->
          </div>
        </span>
      </el-dialog>
      <!-- 确认购买 -->
      <el-dialog title="" :visible.sync="dialogVisible2" @click="dialogVisible2 = false">
        <span class="commodity-info">
          <div class="bigImg">
            <img :src="this.detail.bigPicture" alt="商品展示">
          </div>
          <div class="decs">
            <h1>{{this.detail.formulaName}}</h1>
            <p class="text">规格：{{this.detail.formulaDescription}}</p>
            <p class="price">{{this.detail.formulaPrice}}元-{{this.detail.formulaPrice}}元</p>
            <p>
              <button @click="go">立即购买</button>
              <!-- <button class="green">一键购</button> -->
            </p>
            <!-- <img src="../assets/img/top-cart.png"> -->
          </div>
          <!-- <span slot="footer" class="dialog-footer" @click="dialogVisible2 = false"></span> -->
        </span>
      </el-dialog>
      <!-- 预约时间 -->
      <el-dialog
        class="data-dialog"
        title="预约日期"
        :visible.sync="dialogVisible3"
        @click="dialogVisible3 = false"
      >
        <Calendar @choseDay="clickDay" :agoDayHide="curData" :markDateMore="noAppointTime"></Calendar>
        <el-button
          type="primary"
          class="confirm"
          @click="appointmentTime(1)"
          v-show="confirmAppointTime1"
        >确认</el-button>
        <el-button
          type="primary"
          class="confirm"
          @click="learningAgain(2)"
          v-show="confirmAppointTime2"
        >确认</el-button>
        <el-button
          type="primary"
          class="confirm"
          @click="assistAppointment(3)"
          v-show="confirmAppointTime3"
        >确认</el-button>
      </el-dialog>
    </div>
    <div class="product-description">
      <!-- <ul class="btn">
        <li
          v-for="(item,index) in title"
          :key="index"
          @click="change(index)"
          :class="{'tab':index===curIndex}"
        >{{item}}</li>
      </ul>-->
      <!-- <p>随着近几年餐饮行业的发展，整个餐饮行业收入更是节节攀升，看着餐饮行业大好的市场，不少的人投入到餐饮行业中来，产品挑选则是要进入餐饮行业面临的一大难题。在火锅底料批发中为什么味道是竞争力?赝品从来不会得到人们青睐，这是大多数人找美蛙鱼头底料配料生产的原因之一。这就和当地的饮食消费习惯密切相关了。你的店开在外地，但味道确实是好的火锅味道，因为不符合当地人的口味，再好的味道也很难得到消费者认可。所以你在开店筹备寻找火锅底料批发厂家的过程中，要先了解当地人的消费爱好，找能符合当地人消费习惯的火锅底料味道。</p> -->
      <h3>
        <a href="javascript:;" style="color: black">售后服务专区:</a>
      </h3>
      <h4>
        <a href="javascript:;" @click="learningAgainConfirm">再次学习</a>
        <a href="javascript:;" @click="dialogMaterial = true">底料代炒</a>
        <a href="javascript:;" @click="SetUpShop()">协助开店</a>
      </h4>
      <el-dialog title="底料明细" :visible.sync="dialogMaterial" width="60%">
        <el-table
          :data="tableData"
          max-height="560"
          style="width: 100%"
          :header-cell-style="{
    'background-color': '#fafafa',
    'color': 'rgb(103, 194, 58)',
    'border-bottom': '1px #000000 solid'
}"
        >
          <el-table-column prop="raw_material_name" label="原材料" min-width="20%"></el-table-column>
          <el-table-column prop="variety" label="品种" min-width="20%"></el-table-column>
          <el-table-column prop="unit_price" label="价格" min-width="15%"></el-table-column>
          <el-table-column prop="count" label="重量(kg)" min-width="30%">
            <template scope="scope" ref="wight">
              <el-input-number
                v-model="scope.row.weight"
                placeholder="重量(kg)"
                @change="handleChange(scope.row)"
                :min="1"
                style="width: 50%"
              ></el-input-number>
            </template>
          </el-table-column>
          <el-table-column prop="totalPirce" label="金额(元)" min-width="15%">
            <template scope="scope">
              <div>{{(scope.row.unit_price*scope.row.weight).toFixed(2)}}</div>
            </template>
          </el-table-column>
        </el-table>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogMaterial = false">取 消</el-button>
          <el-button type="primary" @click="goPay()">提交</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import Calendar from "vue-calendar-component";
export default {
  components: {
    Calendar
  },
  data() {
    return {
      active: 0,
      detail: {},
      dialogVisible1: false,
      dialogVisible2: false,
      dialogVisible3: false,
      num: 1,
      checked: false,
      remove: false,
      newDetail: {},
      baseStoreId: "",
      materialDetail: {},
      title: ["描述", "其他相关"],
      curIndex: 0,
      curData: "",
      allowAppointTime: [],
      noAppointTime: [],
      id: "",
      dialogMaterial: false,
      tableData: [],
      confirmAppointTime1: false,
      confirmAppointTime2: false,
      confirmAppointTime3: false,
      newTableData: [],
      totalPirce: 0
    };
  },
  created() {
    this.getData();
    this.disabledDate();
  },
  mounted() {
    this.zoomPic();
  },
  //清除页面缓存
  deactivated() {
    this.$destroy();
  },
  methods: {
    //底料加入购物车
    addToCart() {
      let newDetail = {
        ...this.detail,
        tradeName: this.materialDetail.tradeName,
        price: this.materialDetail.price,
        img: this.materialDetail.bigImage,
        num: this.num,
        checked: this.checked,
        remove: this.remove
      };
      this.newDetail = newDetail;
      if (sessionStorage.getItem("user") == null) {
        this.$message({
          message: "请先登录再购买",
          type: "warning"
        });
      } else {
        this.addGoods();
      }
    },
    addGoods() {
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .post("/order/addGoods", {
          user_phone: userPhone,
          goods_price: this.newDetail.price,
          goods_name: this.newDetail.tradeName,
          goods_img: this.newDetail.img,
          goods_count: this.newDetail.num,
          subtotal: this.newDetail.price * this.newDetail.num
        })
        .then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: "恭喜你，商品已经成功添加",
              type: "success"
            });
            setTimeout(() => {
              this.dialogVisible1 = false;
              this.active = 1;
            }, 1000);
          }
        });
    },
    //获取配方详情
    getData() {
      this.$axios
        .get("/formula/details", {
          params: {
            id: this.$route.params.id
          }
        })
        .then(res => {
          this.detail = res.data;
          this.tableData = this.detail.formulaRawMaterialDOList;
          this.defaultWeight();
          if (sessionStorage.getItem("user")) {
            this.findFormula();
          }
          //不可预约日期对象格式化传入数组 引用类名禁用
          this.detail.practical_operation_noAppointment.forEach(item => {
            let obj = {
              date: item,
              className: "mark1"
            };
            this.noAppointTime.push(obj);
          });
          this.baseStoreId = res.data.baseStoreId;
          this.getMaterialDetail();
        });
    },
    //获取底料详情
    getMaterialDetail() {
      this.$axios
        .get("/baseTrade/details", {
          params: {
            id: this.baseStoreId
          }
        })
        .then(res => {
          this.materialDetail = res.data;
        });
    },
    //已够买配方查询
    findFormula() {
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .get("/formula/queryUserFormulaByUserAndFormula", {
          params: {
            user_phone: userPhone,
            formula_id: this.$route.params.id
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.data !== null) {
            this.id = res.data.data.id;
            if (res.data.status == 200 && res.data.data) {
              this.active = 2;
            }
            if (res.data.data.practical_operation_appointment !== null) {
              this.active = 3;
            }
          }
        });
    },
    //点击购买配方
    go() {
      if (sessionStorage.getItem("user") == null) {
        this.$message({
          message: "请先登录再购买",
          type: "warning"
        });
      } else {
        this.dialogVisible2 = false;
        this.$router.push({
          path: "/payment",
          query: {
            toatalPrice: this.detail.formulaPrice,
            id: this.detail.id
          }
        });
      }
    },
    //修改预约日期提示
    open() {
      this.$confirm("您已经成功预约,确认修改", {
        confirmButtonText: "确认",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          setTimeout(() => {
            this.dialogVisible3 = true;
          }, 1000);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消"
          });
        });
    },
    //选项卡
    change(index) {
      this.curIndex = index;
    },
    //步骤条
    next() {
      if (this.active++ > 5) this.active = 0;
    },
    //当天日期之前的日期不可选
    disabledDate() {
      this.curData = String(
        new Date(new Date().setHours(0, 0, 0, 0)).getTime()
      ).slice(0, -3);
    },
    //选取可预约日期[数组]
    clickDay(data) {
      console.log(data);
      let dataArr = [];
      this.noAppointTime.forEach(item => {
        dataArr.push(item.date);
      });
      let isIncludes = dataArr.includes(data);
      if (isIncludes) {
        this.$message({
          message: "该日期不可预约",
          type: "warning"
        });
      } else {
        this.allowAppointTime.push(data);
      }
    },
    //预约日期方法
    addAppointment(a) {
      this.$axios
        .post("/formula/addAppointment", {
          appointment_type: a,
          id: this.id,
          date: this.allowAppointTime[0]
        })
        .then(res => {
          console.log(res);
          if (res.data.status == 200) {
            this.$message({
              message: "预约成功",
              type: "success"
            });
            this.dialogVisible3 = false;
            //查询操作进度
            this.findFormula();
          }
        });
    },
    //首次日期预约
    appointmentTimeConfirm() {
      this.dialogVisible3 = true;
      this.confirmAppointTime1 = true;
      this.confirmAppointTime3 = false;
      this.confirmAppointTime2 = false;
    },
    appointmentTime(a) {
      if (sessionStorage.getItem("user") == null) {
        this.$message({
          message: "请登录账户",
          type: "warning"
        });
        this.dialogVisible3 = false;
      } else {
        if (this.active < 2) {
          this.$message({
            message: "请先购买配方",
            type: "warning"
          });
        } else {
          this.addAppointment(a);
        }
      }
    },
    //再次学习
    learningAgainConfirm() {
      this.dialogVisible3 = true;
      this.confirmAppointTime1 = false;
      this.confirmAppointTime3 = false;
      this.confirmAppointTime2 = true;
    },
    learningAgain(a) {
      if (sessionStorage.getItem("user") == null) {
        console.log(sessionStorage.getItem("user"));
        this.$message({
          message: "请登录账户",
          type: "warning"
        });
        this.dialogVisible3 = false;
      } else {
        if (a == 2 && this.active <= 2) {
          this.$message({
            message: "请先进行初次学习",
            type: "warning"
          });
        } else {
          this.addAppointment(a);
        }
      }
    },
    //协助开店
    SetUpShop() {
      this.dialogVisible3 = true;
      this.confirmAppointTime1 = false;
      this.confirmAppointTime3 = true;
      this.confirmAppointTime2 = false;
    },
    assistAppointment(a) {
      if (sessionStorage.getItem("user") == null) {
        this.$message({
          message: "请登录账户",
          type: "warning"
        });
        this.dialogVisible3 = false;
      } else {
        this.addAppointment(a);
      }
    },
    //底料代炒
    handleChange(row) {
      this.newTableData.forEach(item => {
        if (item.name == row.raw_material_name) {
          let index = this.newTableData.indexOf(item);
          this.newTableData.splice(index, 1);
        }
      });
      let obj = {
        name: row.raw_material_name,
        variety: row.variety,
        count: row.weight,
        price: row.unit_price,
        img: this.detail.bigPicture,
        subtotal: row.unit_price * row.weight
      };
      this.newTableData.push(obj);
    },
    //默认值重量
    defaultWeight() {
      this.tableData.forEach(item => {
        let obj = {
          name: item.raw_material_name,
          variety: item.variety,
          count: item.weight,
          price: item.unit_price,
          img: this.detail.bigPicture,
          subtotal: item.unit_price * item.weight
        };
        this.newTableData.push(obj);
      });
    },
    //支付页面
    goPay() {
      let user = sessionStorage.getItem("user");
      let order = JSON.stringify(this.newTableData);
      if (user !== null) {
        this.$router.push({
          path: "/payment",
          query: {
            list: order
          }
        });
      } else {
        this.$message({
          message: "请登录账户",
          type: "warning"
        });
        this.dialogMaterial = false;
      }
    }
  },
  watch: {
    $route: {
      handler(n) {
        this.getData();
        this.findFormula();
      },
      deep: true,
      immedial: true
    },
    active: function(value) {}
  }
};
</script>
<style lang='scss'>
.wrap {
  width: 1472px;
  min-height: 1000px;
  .el-input-number {
    .el-input__inner {
      width: 100%;
      padding: 0;
    }
    .el-input-number__decrease {
      width: 30%;
    }
    .el-input-number__increase {
      width: 30%;
    }
  }

  .mark1 {
    color: #bfbfbf;
  }
  .wh_content_all {
    height: 320px !important;
    background-color: #409eff !important;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.5);
  }
  .tab {
    background-color: black;
    color: #ffffff;
  }
  .detail {
    width: 96%;
    height: 530px;
    margin-top: 100px;
    margin-left: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .left-smallImg {
      width: 120px;
      height: 400px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      cursor: pointer;
      img {
        width: 120px;
        height: 120px;
        cursor: pointer;
      }
    }
    .zoom {
      width: 830px;
      height: 400px;
      .smallImg {
        width: 400px;
        height: 400px;
        float: left;
        /* 定位span元素 也即拖拽元素的父元素 */
        position: relative;
        img {
          width: 100%;
          height: 100%;
        }
        .zoomspan {
          position: absolute;
          width: 95px;
          height: 95px;
          top: 0;
          left: 0;
          background: rgba(200, 200, 200, 0.7);
          cursor: move;
          display: none;
        }
      }
      .bigImg {
        width: 400px;
        height: 400px;
        float: right;
        overflow: hidden;
        display: none;
        /* 拖拽元素的父元素 */
        position: relative;
        z-index: 1000;
        img {
          width: 200%;
          height: 200%;
          position: absolute;
        }
      }
    }
  }
  .middle {
    height: 230px;
    margin-left: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    position: relative;
    .detail-decs {
      width: 400px;
      height: 400px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      padding-left: 5px;
      position: absolute;
      top: -538px;
      right: 0px;
      h1 {
        font-size: 30px;
      }
      .text {
        font-size: 20px;
        height: 400x;
        line-height: 40px;
        letter-spacing: 2px;
        span {
          color: #909399;
        }
      }
      .price {
        font-size: 28px;
        font-weight: 600;
        color: #bf0000;
      }
    }
    //进度条样式
    .el-steps {
      width: 90%;
      height: 160px;
      margin: 0 auto;
      position: relative;
      .el-step {
        position: relative;
        margin-top: 45px;
        .el-step__head {
          .el-step__icon {
            width: 40px;
            height: 40px;
            border-radius: 50%;
          }
          .el-step__line {
            height: 5px;
            top: 21px;
            padding-left: 46px;
            .el-step__line-inner {
              height: 100%;
              background-color: #b1cc64;
              border: none;
            }
          }
        }
        .el-step__main {
          .el-step__title {
            width: 123px;
            height: 47px;
            line-height: 47px;
            background-color: #bf0000;
            border-radius: 40px;
            text-align: center;
            color: #ffffff;

            font-size: 12px;
            cursor: pointer;
          }
        }
      }
      // .el-step::after {
      //   content: "真的";
      // }
      .el-step:nth-child(2n + 1) .el-step__main .el-step__title {
        position: absolute;
        top: -100px;
        left: -44px;
      }
      .el-step:nth-child(2n) .el-step__main .el-step__title {
        position: absolute;
        top: 100px;
        left: -40px;
      }
    }
    //按钮样式
    .dialog-btn1 {
      position: absolute;
      top: -20px;
      left: 6px;
      z-index: 1000;
      width: 123px;
      height: 47px;
      border-radius: 40px;
    }
    .dialog-btn2 {
      @extend .dialog-btn1;
      top: 180px;
      left: 149px;
    }
    .dialog-btn3 {
      @extend .dialog-btn1;
      top: -20px;
      left: 293px;
    }
    .dialog-btn4 {
      @extend .dialog-btn1;
      top: 180px;
      left: 445px;
    }
    .dialog-btn5 {
      @extend .dialog-btn1;
      top: -20px;
      left: 589px;
    }
    .dialog-btn6 {
      @extend .dialog-btn1;
      top: 180px;
      left: 742px;
    }
    .next {
      width: 13%;
    }
    //模态框
    .el-dialog {
      width: 60% !important;
      height: 405px;
      .el-dialog__body {
        width: 95%;
        height: 68%;
      }
      .commodity-info {
        width: 83%;
        height: 70%;
        margin-top: 160px;
        margin-left: 40px;
        padding: 0 50px;
        display: flex;
        flex-direction: row;
        position: absolute;
        top: -100px;
        left: 0;
        .bigImg {
          width: 280px;
          height: 280px;
          cursor: pointer;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .decs {
          width: 50%;
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          margin-left: 75px;
          position: relative;
          h1 {
            font-size: 30px;
          }
          .text {
            font-size: 20px;
            height: 40px;
            line-height: 40px;
            letter-spacing: 2px;
