<template>
  <div class="mine-order" v-loading="loading">
    <div class="title" v-show="contentflag">
      <h1>我的川料</h1>
    </div>
    <div class="detail" v-show="contentflag">
      <span
        v-for="(item,index) in title"
        :key="index"
        :class="{activeTab:curIndex==index}"
        @click="changeTab(index)"
      >{{item}}</span>
    </div>
    <div class="table-header" v-show="contentflag">
      <div class="left">订单详情</div>
      <div class="right">
        <div>金额</div>
        <div>收货人</div>
        <div>订单状态</div>
        <div>操作</div>
      </div>
    </div>
    <!-- 购物车部分 -->
    <!--订单 -->
    <div
      class="oneOrder"
      v-for="(item,index) in arry"
      :key="item.order_number"
      v-show="contentflag"
    >
      <div class="order-header">
        <!-- 生成时间 -->
        <span>{{item.create_time}}</span>
        <span>订单号：{{item.order_number}}</span>
        <!-- 删除订单 -->
        <span></span>
      </div>
      <div class="content">
        <div class="commodityinfo" v-for="modifyItem in item.modifyArray" :key="modifyItem.id">
          <!-- 商品信息 -->
          <div class="shoplist">
            <img :src="modifyItem.goods_img" alt="" width="100px" height="100px">
            <!-- 名称 -->
            <span>{{modifyItem.goods_name}}</span>
            <!-- 价格 -->
            <span>￥{{modifyItem.goods_price}}</span>
            <!-- 数量 -->
            <span>x{{modifyItem.goods_count}}</span>
          </div>
        </div>
        <!-- 订单信息 -->
        <div class="order-info">
          <div class="name">
            <span>￥{{item.payment_amount}}</span>
          </div>
          <div class="name">
            <span>{{item.rec_info_name}}</span>
          </div>
          <div class="status">
            <span>{{item.status}}</span>
          </div>
          <div class="handle">
            <el-button
              type="success"
              size="mini"
              @click="rePayment(item)"
              :disabled="item.status == '待发货'? false:true"
            >重新支付</el-button>
            <el-button
              size="mini"
              type="success"
              @click="Confirmreceipt(index,item.order_number)"
              :disabled="item.status == '待收货'? false:true"
            >确认收货</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(index,item.order_number)">删除订单</el-button>
          </div>
        </div>
      </div>
    </div>
    <el-dialog :visible.sync="dialogVisiblezfb" width="30%" :show-close="false">
      <img src="../assets/img/zfb.jpg" alt="">
    </el-dialog>
    <el-dialog :visible.sync="dialogVisiblewx" width="30%" :show-close="false">
      <img src="../assets/img/wx.jpg" alt="">
    </el-dialog>
    <p class="contentflag" v-show="!contentflag">暂时还没有可查看的订单哟~</p>
    <h4 class="typeflag" v-show="typeflag">
      <span>没有{{title[curIndex]}}的订单</span>
    </h4>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: ["全部", "待发货", "待收货", "待评价"],
      order: [],
      accountInformation: {},
      flag: "true",
      loading: true,
      contentflag: true,
      curIndex: 0,
      status: 0,
      arry: [],
      dialogVisiblezfb: false,
      dialogVisiblewx: false,
      typeflag: false
    };
  },
  created() {
    this.findOrder();
  },
  deactivated() {
    this.$destroy();
  },
  methods: {
    //获取用户信息
    getAccountInformation() {
      this.loading = true;
      let userString = sessionStorage.getItem("user");
      this.user_id = JSON.parse(userString).phone;
      this.$axios
        .get("/user/queryUserByPhone?", {
          params: {
            phone: this.user_id
          }
        })
        .then(res => {
          this.accountInformation = res.data.data;
          this.loading = false;
        });
    },
    //获取用户生成的订单
    findOrder() {
      this.loading = true;
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .get("/order/queryOrderList", {
          params: {
            user_phone: userPhone,
            page: 1,
            limit: 10
          }
        })
        .then(res => {
          if (res.status == 200) {
            this.order = res.data.data;
            this.arry = res.data.data;
            // this.buttonDisabled();
            this.order.forEach(item => {
              this.findList(item.order_number);
            });
            this.loading = false;
          }
        });
    },
    //根据订单编号查询商品
    findList(number) {
      this.$axios
        .get("/order/queryOrderGoods", {
          params: {
            order_number: number
          }
        })
        .then(res => {
          if (res.status == 200) {
            let arr = res.data.data;
            this.order.forEach(item => {
              if (item.order_number == arr[0].order_number) {
                this.$set(item, "modifyArray", arr);
              }
            });
          }
        });
    },
    //删除订单
    handleDelete(index, number) {
      this.$axios
        .post("/order/removeOrder", {
          order_number: number
        })
        .then(res => {
          if (res.data.status == 200) {
            this.order.splice(index, 1);
          }
        });
    },
    //选项卡
    changeTab(index) {
      this.curIndex = index;
      if (this.curIndex == 0) {
        var arr = this.order;
      }
      if (this.curIndex == 1) {
        var arr = this.order.filter(item => item.status == "待发货");
      }
      if (this.curIndex == 2) {
        var arr = this.order.filter(item => item.status == "待收货");
      }
      if (this.curIndex == 3) {
        var arr = this.order.filter(item => item.status == "待评价");
      }
      this.arry = arr;
      if (this.arry.length == 0) {
        this.typeflag = true;
      }
    },
    //确认收货
    Confirmreceipt(index, number) {
      this.$axios
        .post("/order/orderConfirmReceipt", {
          order_number: number
        })
        .then(res => {
          if (res.data.status == 200) {
            //修改成功
          }
        });
    },
    //重新支付
    rePayment(item) {
      if (item.payment_method == 1) {
        this.dialogVisiblezfb = true;
      } else {
        this.dialogVisiblewx = true;
      }
    },
  },
  watch: {
    order(val) {
      if (val.length == 0) {
        this.contentflag = false;
        this.typeflag = false;
      }
    }
  }
};
</script>

<style lang="scss" >
.mine-order {
  width: 87%;
  min-height: 1000px !important;
  .title {
    width: 100%;
    text-align: center;
    font-weight: 700;
    h1 {
      font-size: 40px;
    }
  }
  .activeTab {
    color: #bf0000;
  }
  .el-dialog {
    .el-dialog__header {
      padding: 0;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  .detail {
    margin-top: 50px;
    line-height: 40px;
    height: 40px;
    width: 100%;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    span {
      cursor: pointer;
      font-size: 20px;
    }
  }
  .table-header {
    width: 100%;
    height: 56px;
    margin-top: 25px;
    background-color: #f2f2f2;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-top: 1px solid #ebeef5;
    border-right: 1px solid #ebeef5;

    .left {
      width: 42%;
      text-align: center;
    }
    .right {
      width: 47%;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
    }
  }
  .oneOrder {
    width: 100%;
    min-height: 150px;
    margin-bottom: 50px;
    //高度塌陷 用给父元素
    overflow: hidden;
    border-right: 1px solid #ebeef5;
    border-bottom: 1px solid #ebeef5;
    .order-header {
      width: 100%;
      height: 30px;
      line-height: 30px;
      background-color: #daf3ff;
      padding-left: 3%;
      border: 1px solid #ebeef5;
      font-size: 14px;
      span {
        margin-right: 10px;
      }
    }
    .content {
      width: 100%;
      min-height: 100%;
      margin: auto;
      position: relative;
      color: #3c3c3c;
      .commodityinfo {
        width: 53%;
        min-height: 150px;
        float: left;
        border: 1px solid #ebeef5;
        border-bottom: none;
        .shoplist {
          width: 100%;
          min-height: 150px;
          display: flex;
          flex-direction: row;
          justify-content: space-around;
          align-items: center;
          img {
            display: inline-block;
          }
          span {
            display: inline-block;
            margin-right: 20px;
          }
        }
      }
      .order-info {
        width: 47%;
        min-height: 150px;
        float: right;
        position: absolute;
        right: 0;
        top: 10%；;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        border-top: 1px solid #ebeef5;
        .status {
          padding-left: 55px;
        }
        .handle {
          width: 100px;
          height: 100px;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          position: relative;
          .el-button:nth-child(1) {
            width: 90px;
            margin-left: 10px;
          }
        }
      }
    }
  }
  .contentflag {
    width: 91%;
    height: 200px;
    padding: 45px;
    font-size: 20px;
    color: #606266;
    letter-spacing: 1px;
    margin-top: 34px;
  }
  .typeflag {
    width: 100%;
    height: 500px;
    line-height: 500px;
    text-align: center;
    font-size: 16px;
    color: #606266;
    letter-spacing: 1px;
  }
}
</style>