<template>
  <div class="formula common" v-loading="loading">
    <el-row class="mb">
      <el-col :span="10"></el-col>
      <el-col :span="4" :offset="20">
        <p class="sell-btn bg-color1 tc">
          <router-link :to="{name: 'sell'}">我要卖配方</router-link>
        </p>
      </el-col>
      <el-col :span="24" class="tc box" id="videoBox">
        <iframe class="video" width="70%" height="400px" :src="video.url" frameborder="0"></iframe>
      </el-col>
      <el-col :span="24" class="copybox" v-show="isShow"></el-col>
    </el-row>
    <el-row></el-row>
    <ProductShow @load="onLoading"/>
  </div>
</template>

<script>
import "@/utils/jquery/jquery-3.4.1.min.js";
import ProductShow from "@/components/ProductShow";
export default {
  name: "FormulaTrading",
  components: {
    ProductShow
  },
  data() {
    return {
      video: {
        img: "",
        url: "http://player.youku.com/embed/XNDM3NDA4MjgzMg=="
      },
      loading: true,
      flag: false,
      isShow: false
    };
  },
  mounted() {
    this.SuspensionBroadcast();
  },
  methods: {
    onLoading(val) {
      this.loading = val;
    },
    SuspensionBroadcast() {
      var height = $("#videoBox").offset().top + $("#videoBox").height();
      $(window).scroll(function() {
        var bottomHeight =
          $(document).height() - $(window).scrollTop() - $(window).height();
        console.log($(window).scrollTop());
        if ($(window).scrollTop() > height) {
          $("#videoBox").addClass("out");
          $(".copybox").css("display", "block");
        } else if ($(window).scrollTop() < height) {
          $("#videoBox")
            .removeClass("out")
            .addClass("in");
          $(".copybox").css("display", "none");
        }
      });
    }
  }
};
</script>

<style lang="scss">
.formula {
  min-height: 1000px;
  .common-title {
    font-size: 16px;
    padding-bottom: 20px;
  }
  .sell-btn {
    line-height: 36px;
    border-radius: 5px;
    margin-bottom: 20px;
    background-color: #bf0000 !important;
    a {
      color: #fff;
    }
  }
  .el-input__inner {
    background-color: #bf0000;
    color: #ffffff;
  }
  .el-select .el-input .el-select__caret {
    font-size: 20px;
    color: #ffffff;
  }
  .el-select .el-input__inner:focus {
    border-color: #bf0000;
  }
  .tc {
    background-color: black;
    height: 100%;
    margin-bottom: 45px;
  }
  .copybox {
    height: 460px;
  }
  #videoBox.out {
    iframe {
      height: 260px;
    }
    position: fixed;
    bottom: 0;
    right: 0;
    width: 450px;
    height: 260px;
    z-index: 10000;
    animation: an 0.5s;
    transition: 0.5s;
  }
}
</style>
